/**
 * In this file, we create a React component
 * which incorporates components provided by Material-UI.
 */
import React, {Component} from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import {deepOrange500} from 'material-ui/styles/colors';
import FlatButton from 'material-ui/FlatButton';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Tabs, Tab} from 'material-ui/Tabs';
import Slider from 'material-ui/Slider';

const styles = {
  imgContainer: {
    margin: 10
  },
  container: {
    textAlign: 'center',
  },
  image: {
    maxWidth: 400,
    // maxHeight: 400,
    width: 400
  },
  headline: {
    fontSize: 24,
    paddingTop: 16,
    marginBottom: 12,
    fontWeight: 400,
  },
  imagesContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    justifyContent: 'space-between'
  }
};

const muiTheme = getMuiTheme({
  palette: {
    accent1Color: deepOrange500,
  },
});

const ImageContainer = props => (
    <div style={styles.imgContainer}>
      <h2 style={styles.headline}>{props.title}</h2>
      <img style={styles.image} src={props.imgsrc} />
    </div>
);

class Main extends Component {
  constructor(props, context) {
    super(props, context);

    this.handleRequestClose = this.handleRequestClose.bind(this);
    this.handleTouchTap = this.handleTouchTap.bind(this);

    this.state = {
      open: false,
    };
  }

  handleRequestClose() {
    this.setState({
      open: false,
    });
  }

  handleTouchTap() {
    this.setState({
      open: true,
    });
  }



  render() {
    const standardActions = (
      <FlatButton
        label="Ok"
        primary={true}
        onTouchTap={this.handleRequestClose}
      />
    );

    const imgRootPath = "http://cgds.me/schedules/";

    return (<div>

      <MuiThemeProvider muiTheme={muiTheme}>
        <div style={styles.container}>
          <Tabs>
            <Tab label="Stuff" >
              <div>
                <h2 style={styles.headline}>Lorem stuff</h2>
                <p>Lorem ipsum stuff</p>
              </div>
            </Tab>
            <Tab label="Other stuff" >
              <div>
                <h2 style={styles.headline}>Other stuff</h2>
                <p>
                  Other lorem ipsum stuff.
                </p>
              </div>
            </Tab>
            <Tab label="Schedules">
              <div style={styles.imagesContainer}>
                <ImageContainer title="Ali's Schedule" imgsrc={imgRootPath + "ali.jpeg"} />
                <ImageContainer title="Baraa's Schedule" imgsrc={imgRootPath + "baraa.jpeg"} />
                <ImageContainer title="Cagdas's Schedule" imgsrc={imgRootPath + "cagdas.jpg"} />
                <ImageContainer title="Common free tiem for all: " imgsrc={imgRootPath + "bos_zaman.png"} />
                <ImageContainer title="Common free tiem for Ali & Baraa: " imgsrc={imgRootPath + "bos_zaman_ab.png"} />
                <ImageContainer title="Common free tiem for Ali & Cagdas: " imgsrc={imgRootPath + "bos_zaman_ac.png"} />
                <ImageContainer title="Common free tiem for Baraa & Cagdas: " imgsrc={imgRootPath + "bos_zaman_bc.png"} />
              </div>
            </Tab>
          </Tabs>
          <h1>Cok guzel señoriking</h1>
          <h2>Website</h2>
          <RaisedButton
            label="Senior stuff"
            secondary={true}
          />
        </div>
      </MuiThemeProvider></div>
    );
  }
}

export default Main;



